"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import Card from "@/components/ui/card";
import Tabs from "@/components/ui/tabs";
import { authClient } from "@/lib/auth-client";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { useRouter } from "next/navigation";
import { useToast } from "@/components/ui/use-toast";

export default function AuthPage() {
	const [loading, setLoading] = useState<boolean>(false);
	const { toast } = useToast();

	return (
		<Card className="absolute top-1/2 left-1/2 w-full max-w-md -translate-x-1/2 -translate-y-1/2">
			<Card.Header>
				<Card.Title>Welcome</Card.Title>
				<Card.Description>
					Sign in to your account or create a new one
				</Card.Description>
			</Card.Header>
			<Card.Content className="flex flex-col gap-6">
				<div className="grid grid-cols-2 gap-4">
					<Github 
						loading={loading} 
						setLoading={setLoading} 
						toast={toast}
					/>
					<Google 
						loading={loading} 
						setLoading={setLoading} 
						toast={toast}
					/>
				</div>

				<div className="relative text-center">
					<div className="absolute inset-0 flex items-center">
						<span className="w-full border-t" />
					</div>
					<div className="relative flex justify-center text-xs uppercase">
						<span className="bg-background px-2 text-muted-foreground">
							Or continue with
						</span>
					</div>
				</div>

				<Tabs defaultValue="login">
					<Tabs.List className="grid w-full grid-cols-2">
						<Tabs.Trigger value="login">Sign In</Tabs.Trigger>
						<Tabs.Trigger value="register">Sign Up</Tabs.Trigger>
					</Tabs.List>
					<Tabs.Content value="login" className="flex flex-col gap-4">
						<Login loading={loading} setLoading={setLoading} toast={toast} />
					</Tabs.Content>
					<Tabs.Content value="register" className="flex flex-col gap-4">
						<Register loading={loading} setLoading={setLoading} toast={toast} />
					</Tabs.Content>
				</Tabs>
			</Card.Content>
		</Card>
	);
}

type Props = {
	loading: boolean;
	setLoading: (loading: boolean) => void;
	toast: any;
};

